import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IBreadcrumbItem } from './breadcrumb-bar.interface';

@Component({
  selector: 'rx-breadcrumb-bar',
  templateUrl: './breadcrumb-bar.component.html'
})
export class RxBreadcrumbBarComponent {
  @Input() items: IBreadcrumbItem[] = [];

  @Output() selectedItem: EventEmitter<IBreadcrumbItem> = new EventEmitter();

  onSelectItem(item: IBreadcrumbItem) {
    this.selectedItem.emit(item);
  }

  trackByFn(index: number, item: IBreadcrumbItem) {
    return item.label;
  }
}
